@keyframes radiance {
  0% {
    left: -30px;
    margin-left: 0px;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}
@keyframes float {
  0% {
    transform: none;
  }
  33% {
    transform: translateY(-1px) rotate(-2deg);
  }
  66% {
    transform: translateY(1px) rotate(2deg);
  }
  100% {
    transform: none;
  }
}
.header {
  z-index: 99;
  position: relative;
  font-family: "Jost", sans-serif !important;
  background-color: #cdeeff;
  width: 100%;
  box-shadow: 0 8px 16px rgba(88, 84, 82, 0.2), 0 -8px 16px rgba(255, 255, 255, 0.75), inset -1px -1px 0 rgba(255, 255, 255, 0.2);
}
.header--content {
  width: 100%;
  margin: 0 auto;
  padding: 0.5625rem 1rem 1.125rem;
  overflow: hidden;
}
@media screen and (min-width: 415px) {
  .header--content {
    width: 27.875rem;
  }
}
.header--content-logo {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}
.header--content-city {
  font-size: 1rem;
  color: #807773;
  display: flex;
  justify-content: space-between;
  margin-top: 1.125rem;
}
@media screen and (min-width: 375px) {
  .header--content-city {
    font-size: 1.125rem;
  }
}
.header--content-city-name {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 47%;
  height: 2.8125rem;
  border-radius: 1.40625rem;
  box-shadow: 4px 4px 8px rgba(88, 84, 82, 0.15), -4px -4px 8px rgba(255, 255, 255, 0.5), inset -1px -1px 0 rgba(255, 255, 255, 0.2);
}
.header--content-city-name:hover {
  cursor: pointer;
}
.header--content-city-phone {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 47%;
  height: 2.8125rem;
  border-radius: 1.40625rem;
  color: #faf9f8;
  background-color: #fe9800;
  box-shadow: 4px 4px 8px rgba(192, 64, 0, 0.3), -4px -4px 8px rgba(255, 255, 255, 0.5), inset -1px -1px 0 rgba(255, 255, 255, 0.2);
}
.header--content-offer {
  position: relative;
  font-family: "Jost", sans-serif;
  color: #807773;
  margin-top: 1.125rem;
  padding-top: 1.0625rem;
}
.header--content-offer:before {
  content: "";
  height: 100%;
  width: 60%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -1.1rem;
  background-image: url("../images/header_banner.png");
  background-size: auto 100%;
  background-repeat: no-repeat;
}
.header--content-offer-image {
  position: absolute;
  width: 15.0625rem;
  top: 0;
  left: 9.625rem;
}
.header--content-offer-day {
  color: #807773;
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0.375rem;
  text-transform: uppercase;
}
.header--content-offer-ceiling {
  font-size: 1.6875rem;
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.0375rem;
  line-height: 2.25rem;
  text-transform: uppercase;
  margin-top: 0.5625rem;
}
.header--content-offer-today {
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: -0.3px;
  line-height: 18px;
  margin-top: 1rem;
}
.header--content-offer-calendar {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 6rem;
  margin-left: -0.375rem;
  margin-top: 0.3125rem;
}
.header--content-offer-calendar .calendar-day {
  z-index: 10;
  font-weight: 500;
  color: #faf9f8;
  text-transform: uppercase;
  font-size: 0.5rem;
}
.header--content-offer-calendar .calendar-image {
  margin-top: -1.8125rem;
}
.header--content-offer-rush {
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 0.875rem;
  margin-top: 1.25rem;
}
.header--content-form {
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  letter-spacing: -0.01875rem;
