
// 
// Jost
//

/* jost-500 - latin_cyrillic */
@font-face {
    font-family: 'Jost';
    font-display: swap;
    font-style: normal;
    font-weight: 500;
    src: url('../../_webfonts/jost-v1-latin_cyrillic-500.eot'); /* IE9 Compat Modes */
    src: local(''),
        url('../../_webfonts/jost-v1-latin_cyrillic-500.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
        url('../../_webfonts/jost-v1-latin_cyrillic-500.woff2') format('woff2'), /* Super Modern Browsers */
        url('../../_webfonts/jost-v1-latin_cyrillic-500.woff') format('woff'), /* Modern Browsers */
        url('../../_webfonts/jost-v1-latin_cyrillic-500.ttf') format('truetype'), /* Safari, Android, iOS */
        url('../../_webfonts/jost-v1-latin_cyrillic-500.svg#Jost') format('svg'); /* Legacy iOS */
}
